import React from 'react';
import { AlertCircle, Info, CheckCircle, AlertTriangle } from 'lucide-react';
import { Modal } from '@/shared/ui/Modal';
import { Card } from '@/shared/ui/Card';

const PAYMENT_METHODS = [
  {
    id: 'ton',
    name: 'TON Connect',
    description: 'Быстрые и безопасные платежи в TON',
    icon: '💎',
    status: 'active' as const,
    benefits: [
      'Мгновенные транзакции',
      'Минимальная комиссия',
      'Безопасные платежи',
      'Поддержка 24/7'
    ]
  },
  {
    id: 'cash',
    name: 'Наличные',
    description: 'Оплата наличными водителю',
    icon: '💵',
    status: 'active' as const,
    benefits: [
      'Оплата по факту',
      'Без комиссии',
      'Любая сумма',
      'Чек при оплате'
    ]
  },
  {
    id: 'card',
    name: 'Банковская карта',
    description: 'Скоро',
    icon: '💳',
    status: 'soon' as const
  }
];

interface PaymentSelectProps {
  isOpen: boolean;
  onClose: () => void;
  onSelect: (methodId: string) => void;
  selectedMethodId?: string | null;
}

export const PaymentSelect: React.FC<PaymentSelectProps> = ({
  isOpen,
  onClose,
  onSelect,
  selectedMethodId
}) => {
  return (
    <Modal
      isOpen={isOpen}
      onClose={onClose}
      title="Способ оплаты"
    >
      <div className="flex flex-col min-h-[50vh]">
        {/* Payment Methods List */}
        <div className="flex-1 divide-y divide-gray-100">
          {PAYMENT_METHODS.map((method) => {
            const isSelected = selectedMethodId === method.id;
            
            return (
              <button
                key={method.id}
                className={`
                  w-full p-4 transition-colors
                  ${method.status === 'soon'
                    ? 'opacity-50 cursor-not-allowed'
                    : isSelected
                    ? 'bg-tg-secondary'
                    : 'hover:bg-tg-secondary/50 active:bg-tg-secondary/80'
                  }
                `}
                disabled={method.status === 'soon'}
                onClick={() => method.status === 'active' && onSelect(method.id)}
              >
                <div className="flex items-start gap-3">
                  <div className="text-2xl">{method.icon}</div>
                  
                  <div className="flex-1 text-left">
                    <div className="flex items-center gap-2">
                      <span className="font-medium">{method.name}</span>
                      {method.status === 'soon' && (
                        <span className="text-xs px-2 py-0.5 bg-tg-secondary rounded-full text-tg-hint">
                          Скоро
                        </span>
                      )}
                    </div>
                    <div className="text-sm text-tg-hint mt-0.5">
                      {method.description}
                    </div>

                    {method.status === 'active' && method.benefits && (
                      <div className="mt-3 grid grid-cols-2 gap-2">
                        {method.benefits.map((benefit, index) => (
                          <div
                            key={index}
                            className="flex items-center gap-2"
                          >
                            <CheckCircle className="w-4 h-4 text-tg-button" />
                            <span className="text-sm text-tg-text">
                              {benefit}
                            </span>
                          </div>
                        ))}
                      </div>
                    )}
                  </div>
                </div>
              </button>
            );
          })}
        </div>

        {/* Info Sections */}
        <div className="p-4 space-y-3">
          {/* Security Notice */}
          <Card className="bg-blue-50">
            <div className="flex items-start gap-3">
              <AlertTriangle className="w-5 h-5 text-blue-500 flex-shrink-0 mt-0.5" />
              <div className="text-sm">
                <p className="font-medium text-blue-700 mb-1">
                  Безопасные платежи
                </p>
                <p className="text-blue-600">
                  Все платежи проходят через защищенные каналы с использованием современных методов шифрования.
                </p>
              </div>
            </div>
          </Card>

          {/* Coming Soon Notice */}
          <Card className="bg-tg-secondary">
            <div className="flex items-start gap-3">
              <Info className="w-5 h-5 text-tg-hint flex-shrink-0 mt-0.5" />
              <div className="text-sm">
                <p className="font-medium text-tg-text mb-1">
                  Скоро
                </p>
                <p className="text-tg-hint">
                  Оплата банковской картой и другие способы оплаты будут доступны в ближайшее время.
                </p>
              </div>
            </div>
          </Card>
        </div>
      </div>
    </Modal>
  );
};

export {};