// src/screens/MainScreen/components/SideMenu.tsx
import React from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { 
  X, 
  User, 
  Clock, 
  Settings, 
  Info, 
  HelpCircle, 
  Star,
  LogOut
} from 'lucide-react';
import { useApp } from '@/context/AppContext';

interface SideMenuProps {
  isOpen: boolean;
  onClose: () => void;
}

const MENU_ITEMS = [
  { id: 'history', icon: Clock, label: 'История поездок' },
  { id: 'settings', icon: Settings, label: 'Настройки' },
  { id: 'info', icon: Info, label: 'Информация' },
  { id: 'support', icon: HelpCircle, label: 'Служба поддержки' }
];

export const SideMenu: React.FC<SideMenuProps> = ({ isOpen, onClose }) => {
  const { setCurrentScreen } = useApp();

  const handleMenuItemClick = (id: string) => {
    switch (id) {
      case 'history':
        setCurrentScreen('trips');
        onClose();
        break;
      // Добавить другие случаи по мере необходимости
    }
  };

  return (
    <AnimatePresence>
      {isOpen && (
        <>
          {/* Затемнённый фон */}
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            onClick={onClose}
            className="fixed inset-0 bg-black/50 z-40"
          />

          {/* Боковое меню */}
          <motion.div
            initial={{ x: '100%' }}
            animate={{ x: 0 }}
            exit={{ x: '100%' }}
            transition={{ type: 'spring', damping: 25, stiffness: 300 }}
            className="fixed top-0 right-0 w-[80%] max-w-md h-full bg-white z-50"
          >
            {/* Header */}
            <div className="p-4 flex justify-end border-b">
              <button
                onClick={onClose}
                className="p-2 hover:bg-tg-secondary rounded-full"
              >
                <X className="w-6 h-6" />
              </button>
            </div>

            {/* User Info */}
            <div className="p-4 border-b">
              <div className="flex items-center gap-4">
                <div className="w-16 h-16 bg-tg-secondary rounded-full flex items-center justify-center">
                  <User className="w-8 h-8 text-tg-hint" />
                </div>
                <div>
                  <div className="font-medium text-lg">@username</div>
                  <div className="flex items-center gap-1 text-sm text-tg-hint">
                    <Star className="w-4 h-4 text-yellow-400" />
                    <span>4.8</span>
                    <span className="text-xs">• 12 поездок</span>
                  </div>
                </div>
              </div>
            </div>

            {/* Menu Items */}
            <div className="py-2">
              {MENU_ITEMS.map((item) => {
                const Icon = item.icon;
                return (
                  <button
                    key={item.id}
                    className="w-full px-4 py-3 flex items-center gap-3 hover:bg-tg-secondary"
                    onClick={() => handleMenuItemClick(item.id)}
                  >
                    <Icon className="w-5 h-5 text-tg-hint" />
                    <span>{item.label}</span>
                  </button>
                );
              })}
            </div>

            {/* Logout Button */}
            <div className="absolute bottom-0 left-0 right-0 p-4 border-t">
              <button
                className="w-full px-4 py-3 flex items-center gap-3 text-red-500 hover:bg-red-50 rounded-xl"
                onClick={() => {
                  // Добавить логику выхода
                  onClose();
                }}
              >
                <LogOut className="w-5 h-5" />
                <span>Выйти</span>
              </button>
            </div>
          </motion.div>
        </>
      )}
    </AnimatePresence>
  );
};

// Добавляем пустой export для решения проблемы с изолированными модулями
export {};