// src/screens/MainScreen/index.tsx
import React, { useState, useRef, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Header } from './components/Header';
import { ReferralBanner } from './components/ReferralBanner';
import { AddressSearch } from '@/features/locations/components/AddressSearch';
import { PopularPlacesModal } from '@/features/locations/components/PopularPlacesModal';
import { AirportsModal } from '@/features/locations/components/AirportsModal';
import { MapPicker } from '@/features/locations/components/MapPicker';
import { HourlyRatePicker } from '@/features/booking/components/HourlyRatePicker';
import { TripsList } from '@/features/history/components/TripsList';
import { Button } from '@/shared/ui/Button';
import { Card } from '@/shared/ui/Card';
import { Location, Trip } from '@/shared/lib/types/common';
import { useApp } from '@/context/AppContext';

interface MainScreenProps {
  selectedFromPlace: Location | null;
  selectedToPlace: Location | null;
  onPlaceSelect: (place: Location, type: 'from' | 'to') => void;
  onStartBooking: () => void;
  onReferralClick: () => void;
  onTripsClick: () => void;
  onLogout: () => void;
}

interface Address {
  id: string;
  title: string;
  address: string;
  coordinates: {
    lat: number;
    lng: number;
  };
}

export const MainScreen: React.FC<MainScreenProps> = ({
  selectedFromPlace,
  selectedToPlace,
  onPlaceSelect,
  onStartBooking,
  onReferralClick,
  onTripsClick,
  onLogout
}) => {
  // Refs для измерения позиций элементов
  const fromInputRef = useRef<HTMLDivElement>(null);
  const toInputRef = useRef<HTMLDivElement>(null);

  // Состояния
  const [fromAddress, setFromAddress] = useState(selectedFromPlace?.name || '');
  const [toAddress, setToAddress] = useState(selectedToPlace?.name || '');
  const [isMapPickerOpen, setMapPickerOpen] = useState(false);
  const [isHourlyPickerOpen, setHourlyPickerOpen] = useState(false);
  const [isPopularPlacesOpen, setPopularPlacesOpen] = useState(false);
  const [isAirportsOpen, setAirportsOpen] = useState(false);
  const [activePickerType, setActivePickerType] = useState<'from' | 'to'>('from');
  const [isHourlyRate, setIsHourlyRate] = useState(false);
  const [selectedHours, setSelectedHours] = useState<number>(0);

  // Позиции элементов для модальных окон
  const [fromPosition, setFromPosition] = useState({ top: 0, height: 0 });
  const [toPosition, setToPosition] = useState({ top: 0, height: 0 });

  // Измерение позиций элементов
  useEffect(() => {
    const updatePositions = () => {
      if (fromInputRef.current) {
        const rect = fromInputRef.current.getBoundingClientRect();
        setFromPosition({ top: rect.bottom, height: rect.height });
      }
      if (toInputRef.current) {
        const rect = toInputRef.current.getBoundingClientRect();
        setToPosition({ top: rect.bottom, height: rect.height });
      }
    };

    updatePositions();
    window.addEventListener('resize', updatePositions);
    return () => window.removeEventListener('resize', updatePositions);
  }, []);

  // Автоматический переход на BookingScreen
  useEffect(() => {
    if (fromAddress && (toAddress || (isHourlyRate && selectedHours > 0))) {
      onStartBooking();
    }
  }, [fromAddress, toAddress, isHourlyRate, selectedHours]);

  // Обработчики
  const handleAddressSelect = (address: Address, type: 'from' | 'to') => {
    if (type === 'from') {
      setFromAddress(address.title);
      onPlaceSelect({
        id: address.id,
        name: address.title,
        address: address.address,
        type: 'recent',
        coordinates: address.coordinates
      }, 'from');
    } else {
      setToAddress(address.title);
      onPlaceSelect({
        id: address.id,
        name: address.title,
        address: address.address,
        type: 'recent',
        coordinates: address.coordinates
      }, 'to');
    }
  };

  const handleMapConfirm = (location: { lat: number; lng: number; address: string }) => {
    const newPlace: Location = {
      id: `map-${Date.now()}`,
      name: location.address,
      address: location.address,
      type: 'recent',
      coordinates: {
        lat: location.lat,
        lng: location.lng
      }
    };
    
    if (activePickerType === 'from') {
      setFromAddress(location.address);
      onPlaceSelect(newPlace, 'from');
    } else {
      setToAddress(location.address);
      onPlaceSelect(newPlace, 'to');
    }
    setMapPickerOpen(false);
  };

  const handleHourlySelect = (hours: number) => {
    setSelectedHours(hours);
    setIsHourlyRate(true);
    setToAddress('');
    setHourlyPickerOpen(false);
  };

  const handlePopularPlaceSelect = (place: { name: string; address: string }) => {
    handleAddressSelect({
      id: `place-${Date.now()}`,
      title: place.name,
      address: place.address,
      coordinates: { lat: 0, lng: 0 } // В реальном приложении здесь будут реальные координаты
    }, 'to');
  };

  const handleAirportSelect = (airport: any, terminal: any) => {
    const address = `${airport.name}, ${terminal.name}`;
    handleAddressSelect({
      id: `airport-${Date.now()}`,
      title: address,
      address: `${airport.address}, ${terminal.name}`,
      coordinates: { lat: 0, lng: 0 } // В реальном приложении здесь будут реальные координаты
    }, 'to');
  };

  // История поездок
  const recentTrips: Trip[] = [
    {
      id: '1',
      driverId: 'driver-001',
      from: { 
        id: '1', 
        name: 'Красная площадь', 
        address: 'Москва', 
        type: 'popular' 
      },
      to: { 
        id: '2', 
        name: 'Шереметьево', 
        address: 'Терминал D', 
        type: 'airport' 
      },
      date: '24 окт.',
      time: '15:30',
      status: 'completed',
      price: '1800₽',
      carType: 'Комфорт',
      paymentMethod: 'TON Connect'
    }
  ];

  return (
    <div className="min-h-screen bg-gray-50">
      <Header/>
      <ReferralBanner onReferralClick={onReferralClick} />
      
      <div className="p-4 space-y-4">
        {/* Поля поиска адреса */}
        <div className="space-y-2">
          <div ref={fromInputRef}>
            <AddressSearch
              type="from"
              value={fromAddress}
              onChange={setFromAddress}
              onAddressSelect={(address) => handleAddressSelect(address, 'from')}
              onMapClick={() => {
                setActivePickerType('from');
                setMapPickerOpen(true);
              }}
              isRequired={true}
              position={fromPosition}
            />
          </div>

          <div ref={toInputRef}>
            <AddressSearch
              type="to"
              value={toAddress}
              onChange={setToAddress}
              onAddressSelect={(address) => handleAddressSelect(address, 'to')}
              onMapClick={() => {
                setActivePickerType('to');
                setMapPickerOpen(true);
              }}
              isRequired={false}
              onHourlyRateSelect={() => setHourlyPickerOpen(true)}
              position={toPosition}
            />
          </div>

          {isHourlyRate && (
            <div className="px-1 flex items-center justify-between text-sm">
              <span className="text-tg-hint">Почасовая оплата • {selectedHours} ч</span>
              <button 
                className="text-tg-button"
                onClick={() => {
                  setIsHourlyRate(false);
                  setSelectedHours(0);
                }}
              >
                Отменить
              </button>
            </div>
          )}
        </div>

        {/* Быстрый доступ */}
        <div className="grid grid-cols-2 gap-3">
  <Card 
    className="cursor-pointer overflow-hidden relative aspect-[4/3]"
    onClick={() => setPopularPlacesOpen(true)}
  >
    {/* Фоновое изображение */}
    <div 
      className="absolute inset-0 bg-cover bg-center"
      style={{
        backgroundImage: 'url("/images/places/popular-places.jpg")'
      }}
    />
    {/* Градиентный оверлей */}
    <div className="absolute inset-0 bg-gradient-to-b from-black/30 to-black/70" />
    
    {/* Контент */}
    <div className="relative p-4 flex flex-col h-full justify-end">
      <div className="font-medium text-white text-lg">Популярные места</div>
    </div>
  </Card>

  <Card 
    className="cursor-pointer overflow-hidden relative aspect-[4/3]"
    onClick={() => setAirportsOpen(true)}
  >
    {/* Фоновое изображение */}
    <div 
      className="absolute inset-0 bg-cover bg-center"
      style={{
        backgroundImage: 'url("/images/places/airports.jpg")'
      }}
    />
    {/* Градиентный оверлей */}
    <div className="absolute inset-0 bg-gradient-to-b from-black/30 to-black/70" />
    
    {/* Контент */}
    <div className="relative p-4 flex flex-col h-full justify-end">
      <div className="font-medium text-white text-lg">Аэропорты</div>
    </div>
  </Card>
</div>

        {/* История поездок */}
        <div className="space-y-2">
          <div className="flex justify-between items-center px-1">
            <h2 className="text-base text-tg-hint">История поездок</h2>
            <button 
              className="text-tg-button text-sm font-medium"
              onClick={onTripsClick}
            >
              Все поездки
            </button>
          </div>
          
          <TripsList
            trips={recentTrips}
            onTripClick={(trip) => {
              setFromAddress(trip.from.name);
              setToAddress(trip.to.name);
              onPlaceSelect(trip.from, 'from');
              onPlaceSelect(trip.to, 'to');
              setIsHourlyRate(false);
              setSelectedHours(0);
            }}
          />
        </div>
      </div>

      {/* Модальные окна */}
      <MapPicker
        isOpen={isMapPickerOpen}
        onClose={() => setMapPickerOpen(false)}
        onConfirm={handleMapConfirm}
        type={activePickerType}
        initialCoordinates={activePickerType === 'from' ? selectedFromPlace?.coordinates : selectedToPlace?.coordinates}
      />

      <HourlyRatePicker
        isOpen={isHourlyPickerOpen}
        onClose={() => setHourlyPickerOpen(false)}
        onSelect={handleHourlySelect}
      />

      <PopularPlacesModal
        isOpen={isPopularPlacesOpen}
        onClose={() => setPopularPlacesOpen(false)}
        onSelect={handlePopularPlaceSelect}
      />

      <AirportsModal
        isOpen={isAirportsOpen}
        onClose={() => setAirportsOpen(false)}
        onSelect={handleAirportSelect}
      />
    </div>
  );
};

export {};