// src/shared/lib/notifications/store.ts
import { create } from 'zustand';
import type { NotificationType, Notification } from './types';

interface NotificationStore {
  notifications: Notification[];
  addNotification: (notification: Omit<Notification, 'id'>) => string;
  removeNotification: (id: string) => void;
  clearNotifications: () => void;
}

export const useNotifications = create<NotificationStore>((set, get) => ({
  notifications: [],

  addNotification: (notification) => {
    const id = Math.random().toString(36).substr(2, 9);
    const newNotification = { ...notification, id };
    
    set((state) => ({
      notifications: [...state.notifications, newNotification]
    }));

    // Автоматическое удаление уведомления после таймаута
    if (notification.duration !== Infinity) {
      setTimeout(() => {
        get().removeNotification(id);
      }, notification.duration || 5000);
    }

    return id;
  },

  removeNotification: (id) => {
    set((state) => ({
      notifications: state.notifications.filter((n) => n.id !== id)
    }));
  },

  clearNotifications: () => {
    set({ notifications: [] });
  }
}));

// Хук для удобного создания уведомлений
export const useNotificationMethods = () => {
  const { addNotification } = useNotifications();

  const showNotification = (
    type: NotificationType,
    message: string,
    options?: Partial<Omit<Notification, 'id' | 'type' | 'message'>>
  ) => {
    return addNotification({
      type,
      message,
      ...options
    });
  };

  return {
    success: (message: string, options?: Partial<Omit<Notification, 'id' | 'type' | 'message'>>) =>
      showNotification('success', message, options),
    error: (message: string, options?: Partial<Omit<Notification, 'id' | 'type' | 'message'>>) =>
      showNotification('error', message, options),
    info: (message: string, options?: Partial<Omit<Notification, 'id' | 'type' | 'message'>>) =>
      showNotification('info', message, options),
    warning: (message: string, options?: Partial<Omit<Notification, 'id' | 'type' | 'message'>>) =>
      showNotification('warning', message, options)
  };
};