// src/features/locations/components/PopularPlacesModal.tsx
import React from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { MapPin } from 'lucide-react';

interface PopularPlace {
  id: string;
  name: string;
  address: string;
  description?: string;
}

const POPULAR_PLACES: PopularPlace[] = [
  {
    id: 'red-square',
    name: 'Красная площадь',
    address: 'Москва, Красная площадь',
    description: 'Главная площадь страны'
  },
  {
    id: 'gorky-park',
    name: 'Парк Горького',
    address: 'Москва, ул. Крымский Вал, 9',
    description: 'Центральный парк культуры и отдыха'
  },
  {
    id: 'tretyakov',
    name: 'Третьяковская галерея',
    address: 'Москва, Лаврушинский пер., 10',
    description: 'Главный музей русского искусства'
  },
  // Добавьте другие популярные места
];

interface PopularPlacesModalProps {
  isOpen: boolean;
  onClose: () => void;
  onSelect: (place: PopularPlace) => void;
}

export const PopularPlacesModal: React.FC<PopularPlacesModalProps> = ({
  isOpen,
  onClose,
  onSelect,
}) => {
  return (
    <AnimatePresence>
      {isOpen && (
        <>
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 bg-black/50 z-30"
            onClick={onClose}
          />
          <motion.div
            initial={{ y: '100%' }}
            animate={{ y: 0 }}
            exit={{ y: '100%' }}
            transition={{ type: "spring", damping: 25, stiffness: 300 }}
            className="fixed bottom-0 left-0 right-0 bg-white rounded-t-2xl z-30 max-h-[80vh]"
          >
            <div className="p-4 pb-safe">
              <div className="text-lg font-medium mb-4">Популярные места</div>
              <div className="overflow-y-auto space-y-2">
                {POPULAR_PLACES.map((place) => (
                  <button
                    key={place.id}
                    className="w-full p-3 flex items-start gap-3 hover:bg-tg-secondary 
                             rounded-xl transition-colors"
                    onClick={() => {
                      onSelect(place);
                      onClose();
                    }}
                  >
                    <MapPin className="w-5 h-5 text-tg-hint flex-shrink-0 mt-0.5" />
                    <div className="text-left">
                      <div className="font-medium">{place.name}</div>
                      <div className="text-sm text-tg-hint">{place.address}</div>
                      {place.description && (
                        <div className="text-sm text-tg-hint mt-1">{place.description}</div>
                      )}
                    </div>
                  </button>
                ))}
              </div>
            </div>
          </motion.div>
        </>
      )}
    </AnimatePresence>
  );
};