// src/shared/ui/Notifications/NotificationsContainer.tsx
import React from 'react';
import { createPortal } from 'react-dom';
import { NotificationToast } from './NotificationToast';
import { useNotifications } from '@/shared/lib/notifications/store';

export const NotificationsContainer: React.FC = () => {
  const { notifications, removeNotification } = useNotifications();

  if (notifications.length === 0) return null;

  return createPortal(
    <div className="fixed top-4 right-4 left-4 z-50 space-y-2">
      {notifications.map((notification) => (
        <NotificationToast
          key={notification.id}
          notification={notification}
          onClose={() => removeNotification(notification.id)}
        />
      ))}
    </div>,
    document.body
  );
};

// Отдельная функция для создания уведомлений
export const showNotification = {
  success: (message: string, title?: string) => {
    const { addNotification } = useNotifications.getState();
    addNotification({
      type: 'success',
      message,
      title,
      duration: 5000,
    });
  },
  error: (message: string, title?: string) => {
    const { addNotification } = useNotifications.getState();
    addNotification({
      type: 'error',
      message,
      title,
      duration: 5000,
    });
  },
  info: (message: string, title?: string) => {
    const { addNotification } = useNotifications.getState();
    addNotification({
      type: 'info',
      message,
      title,
      duration: 5000,
    });
  },
  warning: (message: string, title?: string) => {
    const { addNotification } = useNotifications.getState();
    addNotification({
      type: 'warning',
      message,
      title,
      duration: 5000,
    });
  },
};

export {};