// src/screens/BookingScreen/index.tsx
import React, { useState } from 'react';
import { ChevronLeft, Clock, Calendar, Car, CreditCard } from 'lucide-react';
import { useApp } from '@/context/AppContext';
import { RouteInfo } from '@/features/booking/components/RouteInfo';
import { CarSelect } from '@/features/booking/components/CarSelect';
import { DateTimeModal } from '@/features/booking/components/DateTimeModal';
import { OrderDetails } from '@/features/booking/components/OrderDetails';
import { PaymentSelect } from '@/features/booking/components/PaymentSelect';
import { RideOptions } from '@/features/booking/components/RideOptions';
import { Button } from '@/shared/ui/Button';
import { Card } from '@/shared/ui/Card';
import type { Location } from '@/shared/lib/types/common';

interface BookingScreenProps {
  onBack: () => void;
  fromPlace: Location;
  toPlace: Location;
}

export const BookingScreen: React.FC<BookingScreenProps> = ({
  onBack,
  fromPlace,
  toPlace
}) => {
  const {
    selectedCarId,
    setSelectedCarId,
    selectedDateTime,
    setSelectedDateTime,
    selectedPaymentMethod,
    setSelectedPaymentMethod,
    isCarSelectOpen,
    setCarSelectOpen,
    isDateTimeModalOpen,
    setDateTimeModalOpen,
    isPaymentSelectOpen,
    setPaymentSelectOpen
  } = useApp();

  const [name, setName] = useState('');
  const [phone, setPhone] = useState('');
  const [selectedOptions, setSelectedOptions] = useState<string[]>([]);

  const canProceed = Boolean(
    selectedCarId &&
    selectedDateTime &&
    selectedPaymentMethod &&
    name.trim() &&
    phone.trim()
  );

  const handleOptionToggle = (optionId: string) => {
    setSelectedOptions(prev =>
      prev.includes(optionId)
        ? prev.filter(id => id !== optionId)
        : [...prev, optionId]
    );
  };

  const handleBooking = async () => {
    try {
      // Здесь будет логика оформления заказа
      console.log('Booking:', {
        fromPlace,
        toPlace,
        selectedCarId,
        selectedDateTime,
        selectedPaymentMethod,
        name,
        phone,
        selectedOptions
      });
    } catch (error) {
      console.error('Booking error:', error);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white px-4 py-3 flex items-center justify-between border-b sticky top-0 z-10">
        <button 
          onClick={onBack}
          className="p-2 hover:bg-tg-secondary rounded-xl -ml-2"
        >
          <ChevronLeft className="w-6 h-6" />
        </button>
        <div className="text-center">
          <div className="text-lg font-medium">Заказ</div>
          <div className="text-sm text-tg-hint">детали поездки</div>
        </div>
        <div className="w-10" /> {/* Для центрирования */}
      </header>

      <div className="p-4 space-y-6">
        {/* Route Info */}
        <RouteInfo
          from={fromPlace}
          to={toPlace}
        />

        {/* Car Selection */}
        <div>
          <div className="font-medium mb-2 px-1">Тип автомобиля</div>
          <Card
            className="flex items-center gap-3 cursor-pointer"
            onClick={() => setCarSelectOpen(true)}
          >
            <div className="w-10 h-10 bg-tg-secondary rounded-xl flex items-center justify-center">
              <Car className="w-5 h-5 text-tg-hint" />
            </div>
            {selectedCarId ? (
              <div className="flex-1">
                <div className="font-medium">Premium</div>
                <div className="text-sm text-tg-hint">2400₽ • 4 места</div>
              </div>
            ) : (
              <div className="flex-1 text-tg-hint">
                Выберите тип автомобиля
              </div>
            )}
          </Card>
        </div>

        {/* Date & Time */}
        <div>
          <div className="font-medium mb-2 px-1">Дата и время</div>
          <Card
            className="flex items-center gap-3 cursor-pointer"
            onClick={() => setDateTimeModalOpen(true)}
          >
            <div className="w-10 h-10 bg-tg-secondary rounded-xl flex items-center justify-center">
              <Clock className="w-5 h-5 text-tg-hint" />
            </div>
            {selectedDateTime ? (
              <div className="flex-1">
                <div className="font-medium">
                  {selectedDateTime.toLocaleDateString('ru-RU', {
                    day: 'numeric',
                    month: 'long'
                  })}
                </div>
                <div className="text-sm text-tg-hint">
                  {selectedDateTime.toLocaleTimeString('ru-RU', {
                    hour: '2-digit',
                    minute: '2-digit'
                  })}
                </div>
              </div>
            ) : (
              <div className="flex-1 text-tg-hint">
                Выберите время подачи
              </div>
            )}
          </Card>
        </div>

        {/* Additional Options */}
        <RideOptions
          selectedOptions={selectedOptions}
          onOptionToggle={handleOptionToggle}
        />

        {/* Contact Info */}
        <OrderDetails
          name={name}
          phone={phone}
          onNameChange={setName}
          onPhoneChange={setPhone}
        />

        {/* Payment Method */}
        <div>
          <div className="font-medium mb-2 px-1">Способ оплаты</div>
          <Card
            className="flex items-center gap-3 cursor-pointer"
            onClick={() => setPaymentSelectOpen(true)}
          >
            <div className="w-10 h-10 bg-tg-secondary rounded-xl flex items-center justify-center">
              <CreditCard className="w-5 h-5 text-tg-hint" />
            </div>
            {selectedPaymentMethod ? (
              <div className="flex-1">
                <div className="font-medium">TON Connect</div>
                <div className="text-sm text-tg-hint">Криптовалюта</div>
              </div>
            ) : (
              <div className="flex-1 text-tg-hint">
                Выберите способ оплаты
              </div>
            )}
          </Card>
        </div>

        {/* Booking Button */}
        <Button
          variant="primary"
          fullWidth
          onClick={handleBooking}
          disabled={!canProceed}
        >
          Заказать за 2400₽
        </Button>
      </div>

      {/* Modals */}
      <CarSelect
        isOpen={isCarSelectOpen}
        onClose={() => setCarSelectOpen(false)}
        onSelect={setSelectedCarId}
        selectedCarId={selectedCarId}
      />

      <DateTimeModal
        isOpen={isDateTimeModalOpen}
        onClose={() => setDateTimeModalOpen(false)}
        onSelect={(date) => {
          setSelectedDateTime(date);
          setDateTimeModalOpen(false);
        }}
        initialDateTime={selectedDateTime}
      />

      <PaymentSelect
        isOpen={isPaymentSelectOpen}
        onClose={() => setPaymentSelectOpen(false)}
        onSelect={setSelectedPaymentMethod}
        selectedMethodId={selectedPaymentMethod}
      />
    </div>
  );
};

export {};