// src/screens/MainScreen/components/Header.tsx
import React, { useState } from 'react';
import { Globe, Menu } from 'lucide-react';
import { SideMenu } from './SideMenu';
import { Modal } from '@/shared/ui/Modal';
import { useApp } from '@/context/AppContext';

const LANGUAGES = [
  { code: 'ru' as const, name: 'Русский' },
  { code: 'en' as const, name: 'English' }
];

export const Header: React.FC = () => {
  const [isLanguageModalOpen, setLanguageModalOpen] = useState(false);
  const [isSideMenuOpen, setSideMenuOpen] = useState(false);
  const { language, setLanguage } = useApp();

  return (
    <>
      <header className="bg-white px-4 py-3 flex items-center justify-between sticky top-0 z-10 border-b">
        {/* Левая часть (пустая для центрирования) */}
        <div className="w-10" />

        {/* Центральная часть (название) */}
        <div className="flex-1 text-center">
          <h1 className="text-xl font-medium text-tg-text">LET'S GO</h1>
        </div>

        {/* Правая часть */}
        <div className="flex items-center gap-2">
          <button 
            className="p-2 hover:bg-tg-secondary rounded-full transition-colors"
            onClick={() => setLanguageModalOpen(true)}
          >
            <Globe className="w-6 h-6 text-tg-hint" />
          </button>
          <button 
            className="p-2 hover:bg-tg-secondary rounded-full transition-colors"
            onClick={() => setSideMenuOpen(true)}
          >
            <Menu className="w-6 h-6 text-tg-hint" />
          </button>
        </div>
      </header>

      {/* Модальное окно выбора языка */}
      <Modal
        isOpen={isLanguageModalOpen}
        onClose={() => setLanguageModalOpen(false)}
        title="Выбор языка"
      >
        <div className="p-4 space-y-2">
          {LANGUAGES.map((lang) => (
            <button
              key={lang.code}
              className={`
                w-full p-3 rounded-xl flex items-center justify-between
                ${language === lang.code ? 'bg-tg-secondary' : 'hover:bg-tg-secondary/50'}
              `}
              onClick={() => {
                setLanguage(lang.code);
                setLanguageModalOpen(false);
              }}
            >
              <span>{lang.name}</span>
              {language === lang.code && (
                <span className="w-2 h-2 rounded-full bg-tg-button" />
              )}
            </button>
          ))}
        </div>
      </Modal>

      {/* Боковое меню */}
      <SideMenu
        isOpen={isSideMenuOpen}
        onClose={() => setSideMenuOpen(false)}
      />
    </>
  );
};