// src/features/booking/components/HourlyRatePicker.tsx
import React from 'react';
import { Clock } from 'lucide-react';
import { Modal } from '@/shared/ui/Modal';
import { motion } from 'framer-motion';

interface HourlyOption {
  hours: number;
  pricePerHour: number;
  totalPrice: number;
}

const HOURLY_OPTIONS: HourlyOption[] = [
  { hours: 1, pricePerHour: 1200, totalPrice: 1200 },
  { hours: 2, pricePerHour: 1100, totalPrice: 2200 },
  { hours: 3, pricePerHour: 1000, totalPrice: 3000 },
  { hours: 4, pricePerHour: 950, totalPrice: 3800 },
  { hours: 5, pricePerHour: 900, totalPrice: 4500 },
  { hours: 6, pricePerHour: 850, totalPrice: 5100 }
];

interface HourlyRatePickerProps {
  isOpen: boolean;
  onClose: () => void;
  onSelect: (hours: number) => void;
}

export const HourlyRatePicker: React.FC<HourlyRatePickerProps> = ({
  isOpen,
  onClose,
  onSelect
}) => {
  return (
    <Modal
      isOpen={isOpen}
      onClose={onClose}
      title="Выберите длительность"
    >
      <div className="p-4 space-y-4">
        <div className="text-sm text-tg-hint text-center">
          Чем дольше поездка, тем ниже цена за час
        </div>
        
        <div className="space-y-2">
          {HOURLY_OPTIONS.map((option) => (
            <motion.button
              key={option.hours}
              className="w-full p-4 bg-white rounded-xl hover:bg-tg-secondary/50
                       transition-colors flex items-center justify-between"
              onClick={() => onSelect(option.hours)}
              whileTap={{ scale: 0.98 }}
            >
              <div className="flex items-center gap-3">
                <div className="w-10 h-10 rounded-xl bg-tg-secondary 
                              flex items-center justify-center">
                  <Clock className="w-5 h-5 text-tg-button" />
                </div>
                <div className="text-left">
                  <div className="font-medium">{option.hours} {getHoursText(option.hours)}</div>
                  <div className="text-sm text-tg-hint">
                    {option.pricePerHour}₽/час
                  </div>
                </div>
              </div>
              <div className="text-right">
                <div className="font-medium">{option.totalPrice}₽</div>
                <div className="text-sm text-tg-hint">всего</div>
              </div>
            </motion.button>
          ))}
        </div>
      </div>
    </Modal>
  );
};

function getHoursText(hours: number): string {
  if (hours === 1) return 'час';
  if (hours >= 2 && hours <= 4) return 'часа';
  return 'часов';
}

export {};