// src/features/booking/components/DateTimeModal.tsx
import React, { useState } from 'react';
import { ChevronLeft, ChevronRight, Clock } from 'lucide-react';
import { Modal } from '@/shared/ui/Modal';
import { Card } from '@/shared/ui/Card';
import { Button } from '@/shared/ui/Button';

interface DateTimeModalProps {
  isOpen: boolean;
  onClose: () => void;
  onSelect: (date: Date) => void;
  initialDateTime?: Date | null;
}

const MONTHS = [
  'Январь', 'Февраль', 'Март', 'Апрель',
  'Май', 'Июнь', 'Июль', 'Август',
  'Сентябрь', 'Октябрь', 'Ноябрь', 'Декабрь'
];

const WEEKDAYS = ['Пн', 'Вт', 'Ср', 'Чт', 'Пт', 'Сб', 'Вс'];

export const DateTimeModal: React.FC<DateTimeModalProps> = ({
  isOpen,
  onClose,
  onSelect,
  initialDateTime
}) => {
  const [selectedDate, setSelectedDate] = useState(
    initialDateTime || new Date()
  );
  const [selectedTime, setSelectedTime] = useState<string>('');
  const [currentMonth, setCurrentMonth] = useState(
    initialDateTime?.getMonth() || new Date().getMonth()
  );
  const [currentYear, setCurrentYear] = useState(
    initialDateTime?.getFullYear() || new Date().getFullYear()
  );

  // Генерация временных слотов
  const generateTimeSlots = () => {
    const slots: string[] = [];
    const currentDate = new Date();
    const isToday = selectedDate.toDateString() === currentDate.toDateString();
    const currentHour = currentDate.getHours();

    for (let hour = 0; hour < 24; hour++) {
      if (isToday && hour < currentHour) continue;
      for (let minute = 0; minute < 60; minute += 30) {
        if (isToday && hour === currentHour && minute <= currentDate.getMinutes()) {
          continue;
        }
        slots.push(`${hour.toString().padStart(2, '0')}:${minute.toString().padStart(2, '0')}`);
      }
    }
    return slots;
  };

  // Генерация дней календаря
  const generateCalendarDays = () => {
    const firstDay = new Date(currentYear, currentMonth, 1);
    const lastDay = new Date(currentYear, currentMonth + 1, 0);
    const days: (number | null)[] = [];

    // Добавляем пустые дни в начало
    let firstDayOfWeek = firstDay.getDay() || 7;
    for (let i = 1; i < firstDayOfWeek; i++) {
      days.push(null);
    }

    // Добавляем дни месяца
    for (let i = 1; i <= lastDay.getDate(); i++) {
      days.push(i);
    }

    return days;
  };

  const handleDateSelect = (day: number) => {
    const newDate = new Date(currentYear, currentMonth, day);
    setSelectedDate(newDate);
  };

  const handleConfirm = () => {
    if (!selectedTime) return;
    
    const [hours, minutes] = selectedTime.split(':').map(Number);
    const date = new Date(selectedDate);
    date.setHours(hours, minutes);
    onSelect(date);
  };

  return (
    <Modal
      isOpen={isOpen}
      onClose={onClose}
      title="Выберите дату и время"
    >
      <div className="p-4 space-y-4">
        {/* Calendar */}
        <Card>
          <div className="flex items-center justify-between mb-4">
            <button
              className="p-2 hover:bg-tg-secondary rounded-xl"
              onClick={() => {
                if (currentMonth === 0) {
                  setCurrentMonth(11);
                  setCurrentYear(prev => prev - 1);
                } else {
                  setCurrentMonth(prev => prev - 1);
                }
              }}
            >
              <ChevronLeft className="w-5 h-5" />
            </button>
            <div className="font-medium">
              {MONTHS[currentMonth]} {currentYear}
            </div>
            <button
              className="p-2 hover:bg-tg-secondary rounded-xl"
              onClick={() => {
                if (currentMonth === 11) {
                  setCurrentMonth(0);
                  setCurrentYear(prev => prev + 1);
                } else {
                  setCurrentMonth(prev => prev + 1);
                }
              }}
            >
              <ChevronRight className="w-5 h-5" />
            </button>
          </div>

          <div className="grid grid-cols-7 mb-2">
            {WEEKDAYS.map(day => (
              <div
                key={day}
                className="h-10 flex items-center justify-center text-sm text-tg-hint"
              >
                {day}
              </div>
            ))}
          </div>

          <div className="grid grid-cols-7 gap-1">
            {generateCalendarDays().map((day, index) => {
              if (day === null) {
                return <div key={`empty-${index}`} />;
              }

              const date = new Date(currentYear, currentMonth, day);
              const isSelected = date.toDateString() === selectedDate.toDateString();
              const isToday = date.toDateString() === new Date().toDateString();
              const isPast = date < new Date(new Date().setHours(0, 0, 0, 0));

              return (
                <button
                  key={day}
                  className={`
                    h-10 rounded-xl flex items-center justify-center text-sm
                    ${isPast ? 'text-tg-hint cursor-not-allowed' :
                      isSelected ? 'bg-tg-button text-white' :
                      isToday ? 'bg-tg-secondary text-tg-button' :
                      'hover:bg-tg-secondary'
                    }
                  `}
                  disabled={isPast}
                  onClick={() => handleDateSelect(day)}
                >
                  {day}
                </button>
              );
            })}
          </div>
        </Card>

        {/* Time slots */}
        <div>
          <div className="flex items-center gap-2 mb-3">
            <Clock className="w-4 h-4 text-tg-hint" />
            <span className="text-sm text-tg-hint">Доступное время</span>
          </div>
          <div className="grid grid-cols-4 gap-2">
            {generateTimeSlots().map(time => (
              <button
                key={time}
                className={`
                  p-3 rounded-xl text-sm transition-colors
                  ${time === selectedTime
                    ? 'bg-tg-button text-white'
                    : 'bg-tg-secondary text-tg-text hover:bg-tg-secondary/80'
                  }
                `}
                onClick={() => setSelectedTime(time)}
              >
                {time}
              </button>
            ))}
          </div>
        </div>

        <Button
          variant="primary"
          fullWidth
          onClick={handleConfirm}
          disabled={!selectedTime}
        >
          Подтвердить
        </Button>
      </div>
    </Modal>
  );
};

export {};