// src/features/locations/components/AirportsModal.tsx
import React, { useState } from 'react';
import { Plane, MapPin, ChevronRight, ChevronLeft } from 'lucide-react';
import { AnimatePresence, motion } from 'framer-motion';

interface Terminal {
  id: string;
  name: string;
  description?: string;
}

interface Airport {
  id: string;
  name: string;
  terminals: Terminal[];
  address: string;
}

const AIRPORTS: Airport[] = [
  {
    id: 'svo',
    name: 'Шереметьево',
    address: 'Московская область, городской округ Химки',
    terminals: [
      { id: 'b', name: 'Терминал B', description: 'Внутренние рейсы' },
      { id: 'c', name: 'Терминал C' },
      { id: 'd', name: 'Терминал D' },
      { id: 'e', name: 'Терминал E' },
      { id: 'f', name: 'Терминал F' }
    ]
  },
  {
    id: 'dme',
    name: 'Домодедово',
    address: 'Московская область, г. Домодедово',
    terminals: [
      { id: '1', name: 'Терминал 1' },
      { id: '2', name: 'Терминал 2' }
    ]
  },
  {
    id: 'vko',
    name: 'Внуково',
    address: 'Москва, ул. 1-я Рейсовая, 12',
    terminals: [
      { id: 'a', name: 'Терминал A' },
      { id: 'b', name: 'Терминал B' }
    ]
  }
];

interface AirportsModalProps {
  isOpen: boolean;
  onClose: () => void;
  onSelect: (airport: Airport, terminal: Terminal) => void;
}

export const AirportsModal: React.FC<AirportsModalProps> = ({
  isOpen,
  onClose,
  onSelect,
}) => {
  const [selectedAirport, setSelectedAirport] = useState<Airport | null>(null);

  return (
    <AnimatePresence>
      {isOpen && (
        <>
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 bg-black/50 z-30"
            onClick={onClose}
          />
          <motion.div
            initial={{ y: '100%' }}
            animate={{ y: 0 }}
            exit={{ y: '100%' }}
            transition={{ type: "spring", damping: 25, stiffness: 300 }}
            className="fixed bottom-0 left-0 right-0 bg-white rounded-t-2xl z-30 max-h-[80vh]"
          >
            <div className="p-4 pb-safe">
              <div className="text-lg font-medium mb-4">
                {selectedAirport ? selectedAirport.name : 'Аэропорты'}
              </div>
              <div className="overflow-y-auto space-y-2">
                {!selectedAirport ? (
                  // Список аэропортов
                  AIRPORTS.map((airport) => (
                    <button
                      key={airport.id}
                      className="w-full p-3 flex items-start gap-3 hover:bg-tg-secondary 
                               rounded-xl transition-colors"
                      onClick={() => setSelectedAirport(airport)}
                    >
                      <MapPin className="w-5 h-5 text-tg-hint flex-shrink-0 mt-0.5" />
                      <div className="text-left flex-1">
                        <div className="font-medium">{airport.name}</div>
                        <div className="text-sm text-tg-hint">{airport.address}</div>
                      </div>
                      <ChevronRight className="w-5 h-5 text-tg-hint" />
                    </button>
                  ))
                ) : (
                  // Список терминалов
                  <>
                    <button
                      className="w-full p-3 flex items-center gap-2 text-tg-button"
                      onClick={() => setSelectedAirport(null)}
                    >
                      <ChevronLeft className="w-5 h-5" />
                      <span>Назад</span>
                    </button>
                    <div className="space-y-2">
                      {selectedAirport.terminals.map((terminal) => (
                        <button
                          key={terminal.id}
                          className="w-full p-3 flex items-start gap-3 hover:bg-tg-secondary 
                                   rounded-xl transition-colors"
                          onClick={() => {
                            onSelect(selectedAirport, terminal);
                            onClose();
                          }}
                        >
                          <MapPin className="w-5 h-5 text-tg-hint flex-shrink-0 mt-0.5" />
                          <div className="text-left">
                            <div className="font-medium">{terminal.name}</div>
                            {terminal.description && (
                              <div className="text-sm text-tg-hint">{terminal.description}</div>
                            )}
                          </div>
                        </button>
                      ))}
                    </div>
                  </>
                )}
              </div>
            </div>
          </motion.div>
        </>
      )}
    </AnimatePresence>
  );
};