// src/features/booking/components/RideOptions.tsx
import React from 'react';
import { Baby, Briefcase, PawPrint, Users, CheckCircle } from 'lucide-react';
import { Card } from '@/shared/ui/Card';

const OPTIONS = [
  {
    id: 'baby-seat',
    icon: Baby,
    title: 'Детское кресло',
    description: 'Для детей до 12 лет',
    price: 300
  },
  {
    id: 'pet',
    icon: PawPrint,
    title: 'Перевозка животных',
    description: 'Специальное место для питомца',
    price: 200
  },
  {
    id: 'luggage',
    icon: Briefcase,
    title: 'Багаж',
    description: 'Дополнительное место для багажа',
    price: 150
  },
  {
    id: 'additional-passenger',
    icon: Users,
    title: 'Дополнительный пассажир',
    description: 'Максимум 4 пассажира',
    price: 0
  }
];

interface RideOptionsProps {
  selectedOptions: string[];
  onOptionToggle: (optionId: string) => void;
}

export const RideOptions: React.FC<RideOptionsProps> = ({
  selectedOptions,
  onOptionToggle
}) => {
  return (
    <div>
      <div className="font-medium mb-2 px-1">Дополнительные опции</div>
      <Card className="divide-y divide-gray-100">
        {OPTIONS.map((option) => {
          const Icon = option.icon;
          const isSelected = selectedOptions.includes(option.id);
          
          return (
            <button
              key={option.id}
              className={`
                w-full p-4 flex items-start gap-3 transition-colors
                ${isSelected ? 'bg-tg-secondary/50' : 'hover:bg-tg-secondary/30'}
              `}
              onClick={() => onOptionToggle(option.id)}
            >
              <div className={`
                w-10 h-10 rounded-xl flex items-center justify-center flex-shrink-0
                ${isSelected ? 'bg-tg-button/10' : 'bg-tg-secondary'}
              `}>
                <Icon className={`w-5 h-5 ${isSelected ? 'text-tg-button' : 'text-tg-hint'}`} />
              </div>
              
              <div className="flex-1 text-left">
                <div className="flex items-center gap-2">
                  <span className="font-medium">{option.title}</span>
                  {isSelected && (
                    <CheckCircle className="w-4 h-4 text-tg-button" />
                  )}
                </div>
                <div className="text-sm text-tg-hint mt-0.5">{option.description}</div>
              </div>
              
              <div className="text-right flex-shrink-0">
                <div className="font-medium">
                  {option.price ? `+${option.price}₽` : 'Бесплатно'}
                </div>
              </div>
            </button>
          );
        })}
      </Card>
    </div>
  );
};

export {};