// src/features/locations/components/AddressSearch.tsx
import React, { useState, useEffect, useRef } from 'react';
import { MapPin, Map, Clock } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';

interface Address {
  id: string;
  title: string;
  address: string;
  coordinates: {
    lat: number;
    lng: number;
  };
}

interface Position {
  top: number;
  height: number;
}

interface AddressSearchProps {
  type: 'from' | 'to';
  value: string;
  onChange: (value: string) => void;
  onAddressSelect: (address: Address) => void;
  onMapClick: () => void;
  isRequired?: boolean;
  onHourlyRateSelect?: () => void;
  position: Position;
}

export const AddressSearch: React.FC<AddressSearchProps> = ({
  type,
  value,
  onChange,
  onAddressSelect,
  onMapClick,
  isRequired = true,
  onHourlyRateSelect,
  position
}) => {
  const inputRef = useRef<HTMLInputElement>(null);
  const [isFocused, setIsFocused] = useState(false);
  const [suggestions, setSuggestions] = useState<Address[]>([]);

  useEffect(() => {
    if (value.length > 2) {
      // В реальном приложении здесь будет API-запрос
      const mockSuggestions: Address[] = [
        {
          id: '1',
          title: 'Улица Ленина, 1',
          address: 'Москва, улица Ленина, 1',
          coordinates: { lat: 55.751244, lng: 37.618423 }
        },
        {
          id: '2',
          title: 'Ленинградский проспект, 10',
          address: 'Москва, Ленинградский проспект, 10',
          coordinates: { lat: 55.751244, lng: 37.618423 }
        }
      ].filter(addr => 
        addr.title.toLowerCase().includes(value.toLowerCase()) ||
        addr.address.toLowerCase().includes(value.toLowerCase())
      );
      
      setSuggestions(mockSuggestions);
    } else {
      setSuggestions([]);
    }
  }, [value]);

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      const target = event.target as HTMLElement;
      if (!target.closest('.suggestions-container') && !inputRef.current?.contains(target)) {
        setIsFocused(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, []);

  return (
    <div className="relative">
      <div className="bg-white p-4 rounded-xl">
        <div className="flex items-center gap-3">
          <MapPin className={`w-5 h-5 ${type === 'from' ? 'text-tg-button' : 'text-red-500'} flex-shrink-0`} />
          <input
            ref={inputRef}
            type="text"
            value={value}
            onChange={(e) => onChange(e.target.value)}
            onFocus={() => setIsFocused(true)}
            placeholder={type === 'from' ? 'Откуда' : 'Куда (необязательно)'}
            className="flex-1 text-tg-text placeholder:text-tg-hint bg-transparent border-0 focus:outline-none focus:ring-0"
          />
          {!isRequired && type === 'to' && (
            <button
              onClick={onHourlyRateSelect}
              className="p-2 hover:bg-tg-secondary rounded-full"
              title="Почасовая оплата"
            >
              <Clock className="w-5 h-5 text-tg-button" />
            </button>
          )}
          <button
            onClick={onMapClick}
            className="p-2 hover:bg-tg-secondary rounded-full"
          >
            <Map className="w-5 h-5 text-tg-button" />
          </button>
        </div>
      </div>

      <AnimatePresence>
        {isFocused && suggestions.length > 0 && (
          <motion.div
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0 }}
            transition={{ duration: 0.2 }}
            className="fixed left-0 right-0 bg-white suggestions-container z-20"
            style={{
              top: type === 'from' ? position.top : position.top,
              height: `calc(100vh - ${position.top}px)`,
              paddingBottom: 'env(safe-area-inset-bottom)'
            }}
          >
            <div className="overflow-y-auto h-full">
              <div className="p-4 space-y-2">
                {suggestions.map((suggestion) => (
                  <button
                    key={suggestion.id}
                    className="w-full p-3 flex items-start gap-3 hover:bg-tg-secondary 
                             rounded-xl transition-colors active:bg-tg-secondary/80"
                    onClick={() => {
                      onAddressSelect(suggestion);
                      setIsFocused(false);
                    }}
                  >
                    <MapPin className="w-5 h-5 text-tg-hint flex-shrink-0 mt-0.5" />
                    <div className="text-left">
                      <div className="font-medium">{suggestion.title}</div>
                      <div className="text-sm text-tg-hint">{suggestion.address}</div>
                    </div>
                  </button>
                ))}
              </div>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
};

export {};