// src/context/AppContext.tsx
import React, { createContext, useContext, useState } from 'react';
import { Location } from '@/shared/lib/types/common';

type Screen = 'main' | 'booking' | 'referral' | 'trips';
type NavigationDirection = 'forward' | 'backward';
type Language = 'ru' | 'en';

interface AppContextType {
  // Навигация
  currentScreen: Screen;
  navigationDirection: NavigationDirection;
  language: Language;
  setLanguage: (lang: Language) => void;
  setCurrentScreen: (screen: Screen) => void;
  goBack: () => void;
  
  // Маршрут
  selectedFromPlace: Location | null;
  selectedToPlace: Location | null;
  setSelectedFromPlace: (place: Location | null) => void;
  setSelectedToPlace: (place: Location | null) => void;

  // Модальные окна
  isPlacePickerOpen: boolean;
  setPlacePickerOpen: (isOpen: boolean) => void;
  isDateTimeModalOpen: boolean;
  setDateTimeModalOpen: (isOpen: boolean) => void;
  isCarSelectOpen: boolean;
  setCarSelectOpen: (isOpen: boolean) => void;
  isPaymentSelectOpen: boolean;
  setPaymentSelectOpen: (isOpen: boolean) => void;

  // Дополнительные состояния
  selectedDateTime: Date | null;
  setSelectedDateTime: (date: Date | null) => void;
  selectedCarId: string | null;
  setSelectedCarId: (id: string | null) => void;
  selectedPaymentMethod: string | null;
  setSelectedPaymentMethod: (method: string | null) => void;
}

const AppContext = createContext<AppContextType | undefined>(undefined);

export const AppProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  // Состояния навигации
  const [currentScreen, setCurrentScreen] = useState<Screen>('main');
  const [navigationDirection, setNavigationDirection] = useState<NavigationDirection>('forward');
  const [screenHistory, setScreenHistory] = useState<Screen[]>(['main']);
  const [language, setLanguage] = useState<Language>('ru');

  // Состояния маршрута
  const [selectedFromPlace, setSelectedFromPlace] = useState<Location | null>(null);
  const [selectedToPlace, setSelectedToPlace] = useState<Location | null>(null);

  // Состояния модальных окон
  const [isPlacePickerOpen, setPlacePickerOpen] = useState(false);
  const [isDateTimeModalOpen, setDateTimeModalOpen] = useState(false);
  const [isCarSelectOpen, setCarSelectOpen] = useState(false);
  const [isPaymentSelectOpen, setPaymentSelectOpen] = useState(false);

  // Дополнительные состояния
  const [selectedDateTime, setSelectedDateTime] = useState<Date | null>(null);
  const [selectedCarId, setSelectedCarId] = useState<string | null>(null);
  const [selectedPaymentMethod, setSelectedPaymentMethod] = useState<string | null>(null);

  // Обработчики навигации
  const handleScreenChange = (screen: Screen) => {
    setScreenHistory(prev => [...prev, screen]);
    setNavigationDirection('forward');
    setCurrentScreen(screen);
  };

  const handleGoBack = () => {
    if (screenHistory.length > 1) {
      const newHistory = [...screenHistory];
      newHistory.pop();
      const previousScreen = newHistory[newHistory.length - 1];
      setScreenHistory(newHistory);
      setNavigationDirection('backward');
      setCurrentScreen(previousScreen);
    }
  };

  // Значение контекста
  const value: AppContextType = {
    // Навигация
    currentScreen,
    navigationDirection,
    language,
    setLanguage,
    setCurrentScreen: handleScreenChange,
    goBack: handleGoBack,

    // Маршрут
    selectedFromPlace,
    selectedToPlace,
    setSelectedFromPlace,
    setSelectedToPlace,

    // Модальные окна
    isPlacePickerOpen,
    setPlacePickerOpen,
    isDateTimeModalOpen,
    setDateTimeModalOpen,
    isCarSelectOpen,
    setCarSelectOpen,
    isPaymentSelectOpen,
    setPaymentSelectOpen,

    // Дополнительные состояния
    selectedDateTime,
    setSelectedDateTime,
    selectedCarId,
    setSelectedCarId,
    selectedPaymentMethod,
    setSelectedPaymentMethod,
  };

  return (
    <AppContext.Provider value={value}>
      {children}
    </AppContext.Provider>
  );
};

// Хук для использования контекста
export const useApp = () => {
  const context = useContext(AppContext);
  if (context === undefined) {
    throw new Error('useApp must be used within an AppProvider');
  }
  return context;
};

export {};