// src/app/App.tsx
import React, { Suspense } from 'react';
import { MainScreen } from '@/screens/MainScreen';
import { BookingScreen } from '@/screens/BookingScreen';
import { ReferralScreen } from '@/screens/ReferralScreen';
import { TripsScreen } from '@/screens/TripsScreen';
import { NotificationsContainer } from '@/shared/ui/Notifications/NotificationsContainer';
import { ErrorBoundary } from '@/shared/components/ErrorBoundary';
import { LoadingState } from '@/shared/components/LoadingState';
import { ScreenTransition } from '@/shared/components/ScreenTransition';
import { useApp } from '@/context/AppContext';
import type { Location } from '@/shared/lib/types/common';

export const App: React.FC = () => {
  const {
    currentScreen,
    selectedFromPlace,
    selectedToPlace,
    setSelectedFromPlace,
    setSelectedToPlace,
    setCurrentScreen,
    navigationDirection,
    goBack
  } = useApp();

  const handlePlaceSelect = (place: Location, type: 'from' | 'to') => {
    if (type === 'from') {
      setSelectedFromPlace(place);
    } else {
      setSelectedToPlace(place);
    }
  };

  const handleStartBooking = () => {
    if (selectedFromPlace && selectedToPlace) {
      setCurrentScreen('booking');
    }
  };
  const handleLogout = () => {
    // Здесь логика выхода
    console.log('Logout clicked');
  };

  const renderScreen = () => {
    return (
      <ScreenTransition direction={navigationDirection}>
        <div className="h-full min-h-screen bg-gray-50">
          <Suspense fallback={<LoadingState fullScreen />}>
            {(() => {
              switch (currentScreen) {
                case 'booking':
                  if (!selectedFromPlace || !selectedToPlace) return null;
                  return (
                    <BookingScreen
                      onBack={goBack}
                      fromPlace={selectedFromPlace}
                      toPlace={selectedToPlace}
                    />
                  );

                case 'referral':
                  return (
                    <ReferralScreen
                      onBack={goBack}
                    />
                  );

                case 'trips':
                  return (
                    <TripsScreen
                      onBack={goBack}
                    />
                  );

                default:
                  return (
                    <MainScreen
                      selectedFromPlace={selectedFromPlace}
                      selectedToPlace={selectedToPlace}
                      onPlaceSelect={handlePlaceSelect}
                      onStartBooking={handleStartBooking}
                      onReferralClick={() => setCurrentScreen('referral')}
                      onTripsClick={() => setCurrentScreen('trips')}
                      onLogout={handleLogout}
                    />
                  );
              }
            })()}
          </Suspense>
        </div>
      </ScreenTransition>
    );
  };

  return (
    <ErrorBoundary>
      <div className="relative">
        {renderScreen()}
        <NotificationsContainer />
      </div>
    </ErrorBoundary>
  );
};

export default App;